.Search{
    font-size: 1.3rem;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 3rem 0.5rem;
    background: -webkit-gradient(linear, left top, right bottom, from(rgb(206, 202, 2)), to(rgba(45, 112, 0, 0.596)));
    background: -webkit-linear-gradient(left top, rgb(206, 202, 2), rgba(45, 112, 0, 0.596));
    background: -o-linear-gradient(left top, rgb(206, 202, 2), rgba(45, 112, 0, 0.596));
    background: linear-gradient(to right bottom, rgb(206, 202, 2), rgba(45, 112, 0, 0.596));
    color: rgb(47, 47, 47);
}
.Search input{
    font-size: 1rem;
    padding: 0.5rem 0.8rem;
    font-weight: 500;
    margin-bottom: 2rem;
   
}
.Search h2{
    font-size: 2.6rem;
    margin-bottom: 0.5rem;
   
}
.noResults{
    margin-top: 1rem;
}

@media screen and (max-width: 600px){
    .Search{
        font-size: 0.9rem;
    }
    .Search input{
        font-size: .9rem;       
    }
    .Search h2{
        font-size: 1.8rem;       
    }

}


